@import '../../../assets/scss/base';

.card_block {
    .small-box {
        padding: 20px;
        @include border-radius (15px);
        @include transition (all, .5s, ease-in-out);
        &.bg-color {
            background: $thead-color;
        }
        .btn-white {
            @include border-radius (10px);
            @include flexCenter;
            margin-right: 15px;
            padding: 5px;
            min-width: 48px;
            min-height: 48px;
            img {
                width: 24px;
            }
        }
        &:hover {
            box-shadow: 0px 1px 16px rgba(92, 92, 92, 0.23);
        }
    }
    &:nth-child(2) {
        .small-box {
            background: #E0CDF6;
        }
    }
    &:nth-child(3) {
        .small-box {
            background: #F7CAC6;
        }
    }
    &:nth-child(3) {
        .small-box {
            background: #EEB0CC;
        }
    }
}
.info-box-content {
    .h5 {
        margin: 0;
        color: $base-color;
        font-weight: $font-bold;
    }
}