@import '../../../assets/scss/base';

.table-responsive {
    .table {
        border-collapse: separate;
        border-spacing: 0 10px;
        color: $base-color;
        margin: 0;
        thead {
            th {
                background: $thead-color;
                font-weight: $font-semibold;
                text-transform: $capitalize;
                @include font-size (1);
                white-space: nowrap;
                color: $base-color;
                padding: .70rem 2rem .70rem 1rem;
                border: 0;
                position: $relative;
                &:first-child {
                    @include border-radius(7px 0 0 7px);
                }
                &:last-child {
                    @include border-radius(0 7px 7px 0);
                }
            }
        }
        tbody {
            tr {
                td {
                    padding: .40rem 1rem;
                    @include font-size (1);
                    border-width: 1px 0;
                    background: $grey-color-400;
                    border-color: $grey-color-700;
                    vertical-align: middle;
                    position: $relative;
                    white-space: nowrap;
                    color: $base-color;
                    height: 44px;
                    &:first-child {
                        border-left-width: 1px;
                        @include border-radius(7px 0 0 7px);
                    }
                    &:last-child {
                        border-right-width: 1px;
                        @include border-radius(0 7px 7px 0);
                        &::after {
                            display: $none;
                        }
                    }
                    &::after {
                        content: '';
                        position: $absolute;
                        right: 0;
                        top: 5px;
                        background: $grey-color-700;
                        height: calc(100% - 10px);
                        width: .5px;
                        z-index: 1;
                    }
                    .btn-sm {
                        min-height: 44px;
                    }
                }
                .btn-grey {
                    background: $white-color;
                }
            }
            &:nth-child(even) {
                td {
                    background: $white-color;
                    .btn-grey {
                        background: $grey-color-400;
                    }
                }
            }
            &:hover {
                td {
                    background: $grey-color-300;
                }
            }
            .react-select {
                max-width: 140px;
                // z-index: 1;
            }
        }
    }
    .table-white {
        width: 100%;
        tr {
            th,
            td {
                background: $white-color;
                padding: .75rem 0;
                border-bottom: 1px solid $grey-color-700;
            }
            &:last-child {
                td,
                th {
                    border-bottom: 0;
                }
                th {
                    font-weight: $font-medium;
                }
            }
        }
    }
}


.sort_icon {
    position: $absolute;
    right: 5px;
    &.active {
        @include transform (rotate(-180deg));
        @include transform-origin (50% 57%);
        display: $inline-block;
    }
}