@import '../assets/scss/base';

// Left Sidebar
.main {
  &.slide-active {
    @media (max-width:1200px) {
      &::before {
        content: '';
        background: rgba($black-color, .5);
        width: 100%;
        height: 100%;
        position: $fixed;
        left: 0;
        top: 0;
        z-index: 1111;
      }
    }
    .sideBar {
      left: 0;
    }
  }
  .content {
    min-height: 100vh;
    @include transition (all, 0.5s, ease-in-out);
    @media (min-width:1201px) {
      margin-left: 0;
    }
  }
  &.slide-active {
    .content {
      @media (min-width:1200px) {
        margin-left: 320px;
      }
    }
  }
}

.sideBar {
  @include border-radius(0 30px 30px 0);
  background-color: $white-color;
  border-right: 1px solid $grey-color-700;
  position: $fixed;
  top: 0;
  left: -320px;
  bottom: 0;
  width: 320px;
  height: 100%;
  padding: 0 20px;
  z-index: 999;
  @include transition (all, 0.5s, ease-in-out);
  background-image: url(../assets/images/nurse_vector.png);
  background-repeat: $no-repeat;
  background-position: $center $bottom 10px;
  z-index: 1111;
  @media (max-width:1200px) {
    left: -320px;
  }
  @at-root {
    .navbar-brand {
      text-align: $left;
      width: 100%;
      cursor: pointer;
      margin: 0 0 0 20px;
      img {
        width: 200px;
      }
    }
    .close_btn {
      background: $transparent;
      border: 1px solid $base-color;
      @include border-radius (50%);
      min-height: 45px;
      min-width: 45px;
      position: $absolute;
      top: 10px;
      right: 10px;
      z-index: 1;
      &:hover {
        background: $primary-color;
        img {
          filter: brightness(0) invert(1);
        }
      }
    }
  }
  .navbar {
    height: 100%;
    .navbar-nav {
      height: calc(100% - 90px);
      overflow-x: $hidden;
      overflow-y: $auto;
      li {
        cursor: $pointer;
        padding: 1rem 1.3rem;
        display: $block;
        color: $base-color;
        font-size: 1rem;
        @include border-radius (10px);
        @include transition (all, .5s, ease-in-out);
        margin: 1px 0;
        &.active,
        &:hover {
          background: $primary-color;
          color: $white-color;
          em {
            img {
              filter: brightness(0) invert(1);
              -webkit-filter: brightness(0) invert(1);
            }
          }
        }
        em {
          min-width: 25px;
          text-align: $center;
          display: $inline-block;
          img {
            max-width: 24px;
            max-height: 28px;
            vertical-align: middle;
          }
        }
      }
    }
  }
}


.close {
  height: 25px;
  width: 25px;
  position: $absolute;
  right: 0;
  margin: 5px;
}