@import '../../../assets/scss/base';

// Header
.header {
    border-bottom: 1px solid $grey-color-700;
    margin: 0 0 25px;
    padding: 15px 0;
    position: $relative;
    z-index: 111;
    @at-root {
        .hamburger {
            background: $grey-color-300;
            @include border-radius (50%);
            text-align: $center;
            min-width: 50px;
            min-height: 50px;
            padding: 10px;
            border: 0;
            @include flexCenter;
            &:hover {
                background: $primary-color;
                img {
                    filter: brightness(0) invert(1);
                }
            }
            img {
                display: $inline-block;
            }
        }
    }
}

.logo_icon {
    width: 50px;
    cursor: $pointer;
}
.slide-active {
    .logo_icon {
        @media (min-width:1201px) {
            display: $none;
        }
    }
}

.noficaition_btn {
    @include border-radius (50%);
    background: $grey-color-700;
    text-align: $center;
    min-width: 50px;
    min-height: 50px;
    max-height: 50px;
    padding: 5px;
    border: 0;
    position: $relative;
    &:hover {
        background: $grey-color-400;
    }
    img {
        width: 22px;
    }
    .badge {
        background: $error-color;
        @include border-radius (50%);
        @include font-size (.50);
        @include flexCenter;
        min-width: 20px;
        min-height: 20px;
        color: $white-color;
        position: $absolute;
        right: 5px;
        top: 5px;

    }
}

.avtar {
    @include border-radius(50%);
    background-color: $grey-color-300;
    border: 1px solid $primary-color;
    overflow: $hidden;
    img {
        object-fit: $cover;
        width: 100%;
        height: 100%;
    }
}

.arrow_down {
    margin-left: 10px;
    img {
        min-width: 18px;
    }
}

.author_info {
    margin: $auto 0;
    .h6 {
        @include font-size(1);
        font-weight: $font-bold;
        margin: 0;
    }
    small {
        @include font-size(.85);
        font-weight: $font-semibold;
    }
}

// DropMenu
.notification_drop {
    .dropDown {
        min-width: 326px;
        right: calc(50% - 326px/2);
        &::before {
            content: '';
            position: $absolute;
            top: -8px;
            left: calc(50% - 10px);
            height: 15px;
            width: 15px;
            @include border-radius (6px 0px 0px 0px);
            @include transform (rotate(45deg));
            background: $white-color;
        }
    }
}

.dropDown {
    position: $absolute;
    top: calc(100% + 10px);
    right: 0;
    min-width: 250px;
    @include transition (all, 0.5s, ease-in-out);
    background: $white-color;
    border: 1px solid #e8e8e8;
    box-shadow: 0 0 10px rgb(0, 0, 0, .10);
    @include border-radius (10px);
    z-index: 1;
    .user-body {
        max-height: 280px;
        overflow-x: $hidden;
        overflow-y: $auto;
    }
    &-list {
        border-bottom: 1px solid $grey-color-300;
        padding: 10px 15px;
        @include flexbox;
        @include font-size (1);
        @include align-items ($center);
        text-transform: $capitalize;
        cursor: $pointer;
        em {
            height: 40px;
            width: 40px;
            padding: 4px;
            margin-right: 10px;
            background: $grey-color-700;
            @include border-radius (50%);
            text-align: $center;
            @include flexbox;
            @include align-items ($center);
            @include justify-content ($center);
            img {
                min-width: 20px;
                max-width: 23px;
                max-height: 20px;
            }
        }
        span {
            font-weight: $font-semibold;
            text-transform: $capitalize;
            margin: $auto 0;
            @include font-size (1);
            color: $base-color;
        }
        &:hover {
            span {
                color: $secondary-color;
            }
        }
    }
    .dropdown {
        &-header {
            padding: 20px 70px;
            @include border-radius (10px 10px 0 0);
            // box-shadow: 0px 2px 10px -2px rgba($black-color, .2);
            position: $relative;
            .h6 {
                @include font-size (1.2);
                font-weight: $font-semibold;
                color: $primary-color;
            }
            .btn {
                padding: 0 10px;
                line-height: 1;
                color: $base-color;
                position: $absolute;
                font-size: 12px;
                font-weight: $font-semibold;
                top: calc(50% - 8px);
                right: 0;
                &:hover {
                    text-decoration: $underline;
                }
            }
        }
        &-body {
            padding: 0 15px;
            max-height: 270px;
            overflow-y: $auto;
            margin: 0;
            li {
                border: 1px solid $grey-color-300;
                background: $grey-color-500;
                @include border-radius (10px);
                margin-bottom: 10px;
                padding: 13px;
                color: $base-color;
                &:last-child {
                    margin-bottom: 0
                }
                .h6 {
                    font-weight: $font-semibold;
                    @include font-size (1);
                    color: $inherit;
                    margin: 0 0 4px;
                    small {
                        @include font-size (.8);
                        @include opacity (.6);
                        white-space: nowrap;
                        color: $inherit;
                        margin-left: 10px;
                    }
                }
                p {
                    color: $inherit;
                    @include opacity (.6);
                    @include font-size (.99);
                }
            }
        }
        &-footer {
            @include border-radius (0 010px 10px);
            padding: 12px 15px;
            .btn {
                font-weight: $font-semibold;
                text-decoration: $underline;
                &:hover {
                    text-decoration: $none;
                }
            }
        }
    }
}