@import '../../../assets/scss/base';

.confirmation_modal {
    @media (min-width:1200px) {
        padding: 0 10px;
    }
    em {
        width: 70px;
        height: 70px;
        padding: 10px;
        margin: 0 $auto;
        @include flexCenter;
        text-align: $center;
        @include border-radius (50%);
        background-color: $grey-color-300;
        @include border-radius (50%);
        img {
            width: 30px;
        }
    }
}

.modal_icon {
    &.success_icon {
        background: #F3FAEC;
    }
    &.danger_icon {
        background: rgba($error-color, .4);
    }
}