@import '../../../assets/scss/base';

.close_crop {
    border: 0;
    position: $absolute;
    top: 10px;
    right: 10px;
    z-index: 999;
    padding: 4px;
    width: 40px;
    height: 40px;
    background: $white-color;
    @include border-radius (50%);
    @include flexCenter;
    img {
        width: 30px;
    }
}
.save_btn {
    width: 100%;
    z-index: 999;
    bottom: 10px;
}

.profile_pic {
    position: $relative;
}

.upload_icon {
    position: $absolute;
    background: $primary-color;
    @include border-radius (50%);
    padding: 2px;
    text-align: $center;
    @include flexCenter;
    width: 30px;
    height: 30px;
    right: 0;
    bottom: 0px;
    overflow: $hidden;
    z-index: 1;
    input[type="file"] {
        position: $absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @include opacity (0);
        z-index: 1;
    }
    img {
        width: 15px;
    }
}