@import '../../../assets/scss/base';

.tab_full {
    border-bottom: 1px solid $grey-color-700;
}
.nav-tabs {
    overflow-y: $hidden;
    overflow-x: $auto;
    border: 0;
    li {
        border: 0;
        padding: 22px 0;
        cursor: $pointer;
        margin: 0 35px 0 0;
        @include font-size (1.05);
        color: rgba($base-color, .5);
        position: $relative;
        &::before {
            content: '';
            width: 0;
            @include opacity (0);
            visibility: $hidden;
            height: 4px;
            position: $absolute;
            bottom: -1px;
            left: 0;
            background: $base-color;
            @include border-radius (6px);
            @include transition (all, .5s, ease-in-out);
        }
        &:hover,
        &.active {
            color: $base-color;
            &::before {
                width: 100%;
                @include opacity(1);
                visibility: $visible;
            }
        }
        &.active {
            font-weight: $font-bold;
        }
        &:last-child {
            margin-right: 0;
        }
    }
}