@import '../../../assets/scss/base';

.react {
    &-select {
        min-width: 145px;
        .react {
            &-prefix {
                &__indicator {
                    padding: 8px 8px 8px 0;
                    &-separator {
                        display: $none;
                    }
                    svg {
                        path {
                            fill: $base-color;
                        }
                    }
                }
                &__placeholder {
                    font-weight: $font-semibold;
                    color: $base-color;
                }
                &__single-value {
                    font-weight: $font-semibold;
                }
                &__control {
                    border-color: $grey-color-700;
                    @include border-radius (10px);
                    box-shadow: $none !important;
                    height: 48px;
                    &:hover {
                        border-color: $grey-color-700;
                    }
                }
                &__input-container {
                    font-weight: $font-semibold;
                    color: $base-color;
                }
                &__value-container {
                    padding: 2px 0px 2px 15px;
                }
                &__option {
                    @include font-size (1);
                }
            }
        }
    }
}
.green {
    .react-prefix {
        &__control {
            background-color: rgba($badge-success, .2);
            &:hover {
                border-color: $badge-success;
            }
        }
        &__single-value {
            color: $success-color;
        }
    }
}
.red {
    .react-prefix {
        &__control {
            background-color: rgba($error-color, .3);
            &:hover {
                border-color: $error-color;
            }
        }
        &__single-value {
            color: $error-color1;
        }
    }
}
.admin_action {
    .react {
        &-select {
            margin-left: 10px;
            min-width: 130px;
            z-index: 11;
            .react {
                &-prefix {
                    &__placeholder {
                        color: $base-color;
                    }
                    &__indicator {
                        svg {
                            path {
                                fill: $base-color;
                            }
                        }
                    }
                    &__control {
                        height: 48px;
                        color: $base-color;
                        @include font-size (1);
                        background: $transparent;
                        border: 2px solid $base-color;
                    }
                }
            }
        }
    }
    .btn-sm {
        min-width: 120px;
        @include border-radius (10px);
    }
}