@import '../../../assets/scss/base';

/* Pagination */
.pagination {
    margin: 0;
    list-style: $none;
    li {
        margin: 0 3px;
        &.previous a,
        &.next a {
            background-image: url(../../../assets/icons/arrow_rht.svg);
            background-position: $center;
            background-repeat: $no-repeat;
            background-size: 8px;
            border: 1px solid $primary-color;
            font-size: 0
        }
        &.previous {
            @include transform (scale(-1));
        }
        a {
            background-color: $white-color;
            border-radius: 3px;
            width: 32px;
            height: 32px;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 5px;
            border: 0;
            color: $base-color;
            text-align: center;
            border: 1px solid $grey-color-700;
            font-size: 15px;
            font-weight: $font-semibold;
        }
        &:hover,
        &.selected {
            a {
                border: 1px solid $primary-color;
                background-color: $white-color;
                color: $primary-color;
            }
        }
        &.disabled {
            a {
                pointer-events: $none;
                @include opacity (.5);
            }
        }
    }
}

.pagination li:hover .dots {
    pointer-events: none;
    border-color: $grey-color-700;
    color: $base-color;
}

.pageCount {
    min-width: 250px;
    @include transition (all, 0.5s, ease-in-out);
    background: $white-color;
    border: 1px solid #e8e8e8;
    box-shadow: 0 0 10px rgb(0, 0, 0, .10);
    @include border-radius (10px);
    .user-body {
        max-height: 280px;
        overflow-x: $hidden;
        overflow-y: $auto;
    }
    &-list {
        border-bottom: 1px solid $grey-color-300;
        padding: 15px;
        @include flexbox;
        @include align-items ($center);
        text-transform: $capitalize;
        cursor: $pointer;
        em {
            height: 40px;
            width: 40px;
            padding: 4px;
            margin-right: 10px;
            background: $grey-color-700;
            @include border-radius (50%);
            text-align: $center;
            @include flexbox;
            @include align-items ($center);
            @include justify-content ($center);
            img {
                min-width: 20px;
                max-width: 23px;
                max-height: 20px;
            }
        }
        span {
            font-weight: $font-regular;
            text-transform: $capitalize;
            margin: $auto 0;
            @include font-size (1);
            color: $base-color;
        }
        &:hover {
            span {
                color: $secondary-color;
            }
        }
    }
    .dropdown {
        &-header {
            padding: 20px 70px;
            @include border-radius (10px 10px 0 0);
            // box-shadow: 0px 2px 10px -2px rgba($black-color, .2);
            position: $relative;
            .h6 {
                @include font-size (1.2);
                font-weight: $font-semibold;
                color: $primary-color;
            }
            .btn {
                padding: 0 10px;
                line-height: 1;
                color: $base-color;
                position: $absolute;
                font-size: 12px;
                font-weight: $font-semibold;
                top: calc(50% - 8px);
                right: 0;
                &:hover {
                    text-decoration: $underline;
                }
            }
        }
        &-body {
            padding: 0 15px;
            max-height: 270px;
            overflow-y: $auto;
            margin: 0;
            li {
                border: 1px solid $grey-color-300;
                background: $grey-color-500;
                @include border-radius (10px);
                margin-bottom: 10px;
                padding: 13px;
                color: $base-color;
                &:last-child {
                    margin-bottom: 0
                }
                .h6 {
                    font-weight: $font-semibold;
                    @include font-size (1);
                    color: $inherit;
                    margin: 0 0 4px;
                    small {
                        @include font-size (.8);
                        @include opacity (.6);
                        white-space: nowrap;
                        color: $inherit;
                        margin-left: 10px;
                    }
                }
                p {
                    color: $inherit;
                    @include opacity (.6);
                    @include font-size (.99);
                }
            }
        }
        &-footer {
            @include border-radius (0 010px 10px);
            padding: 12px 15px;
            .btn {
                font-weight: $font-semibold;
                text-decoration: $underline;
                &:hover {
                    text-decoration: $none;
                }
            }
        }
    }
}

.dropdown_entries {
    box-shadow: 0 0 10px rgba($black-color, .1);
    border: 1px solid $grey-color-700;
    @include border-radius (7px);
    background: $white-color;
    margin: 2px 0 0;
    left: 0;
    text-align: $center;
    position: $absolute;
    top: 100%;
    min-width: 80px;
    .list {
        border-bottom: 1px solid $grey-color-700;
        cursor: $pointer;
        padding: 3px 0;
        &:last-child {
            border-bottom-color: $transparent;
        }
        &:hover {
            color: $secondary-color;
        }
    }
}

.data_table_info {
    @include font-size (.9);
    .white_control {
        height: 38px;
    }
}