@import "src/assets/scss/base";

.front_form {
  background: radial-gradient(
    63.91% 63.91% at 50% 0%,
    #20616e 0%,
    #114752 100%
  );
  padding: 30px 20px;
  @include flexbox();
  @include align-items($center);
  @include justify-content($center);
  isolation: isolate;
  width: 100%;
  z-index: 1;
  .vector_shape {
    position: $fixed;
    width: 90%;
    bottom: -100px;
    left: 0;
    z-index: -1;
    svg,
    path {
      width: 100%;
      height: 100%;
    }
  }
}

.ilustrator {
  width: 100%;
  display: $inline-block;
  text-align: $center;
  max-width: 650px;
  padding: 20px;
  img {
    width: 100%;
  }
}

.ls_form {
  margin: 0 $auto;
  background: $white-color;
  @include border-radius (20px);
  padding: 30px;
  @media (min-width:1200px){
    padding: 55px;
    max-width: 600px;
  }
  @at-root .logo_front {
    display: $inline-block;
    margin: 0 0 20px;
  }
  .h6 {
    // @include font-size (1.4);
    color: $base-color;
  }
  form {
    padding: 30px 0 0;
  }
  a {
    cursor: $pointer;
    @include font-size(1.1);
    text-decoration: $underline;
    &:hover {
      text-decoration: $none;
    }
  }
  .btn-md {
    min-width: 200px;
  }
  .form-control {
    padding-right: 55px;
  }
}

.show_hide_pwd {
  position: $absolute; top: calc(50% - 30px / 2); right: 20px; border: 0; background: $transparent;
  cursor: $pointer; z-index: 1;
  img {width: 24px;}
}
.forgot_txt {
  span {
    &:hover {
      text-decoration: $underline;
    }
  }
}
.pointer {
  cursor: $pointer;
}
