// _theme.scss

html {
  scroll-behavior: smooth;
  height: 100%;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  background: $grey-color-500;
  font-family: $default-font;
  font-weight: $font-regular;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $base-color;
  @include font-size(1.1);
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
ul {
  margin: 0;
  padding: 0;
}
input {
  outline: 0 !important;
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-weight: $font-bold;
  line-height: 1.2;
}
h1,
.h1 {
  @include font-size(4);
  @media (max-width: 991px) {
    @include font-size(3);
  }
  @media (max-width: 767px) {
    @include font-size(2.6);
  }
}
h2,
.h2 {
  @include font-size(2.5);
  @media (max-width: 991px) {
    @include font-size(2.3);
  }
}
h3,
.h3 {
  @include font-size(2.5);
  @media (max-width: 991px) {
    @include font-size(2.1);
  }
}
h4,
.h4 {
  @include font-size(2.3);
}
h5,
.h5 {
  @include font-size(1.5);
}
h6,
.h6 {
  @include font-size(1.2);
  font-weight: $font-semibold;
}

p {
  color: $base-color;
  &:last-child {
    margin-bottom: 0;
  }
}
img {
  border: 0;
  max-width: 100%;
  outline: 0 !important;
  @include no-select;
}
a {
  cursor: $pointer;
  color: $primary-color;
  text-decoration: $none;
  @include transition(all, 0.5s, ease-in-out);
  &:hover {
    text-decoration: $none;
    color: $base-color;
  }
}

/* Scroll */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
/* Track */
::-webkit-scrollbar-track {
  background: $primary-color;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: $prussian-color;
  @include border-radius(10px);
}
.grey-bg {
  background: $grey-color-500;
  border: 1px solid $grey-color-700;
}
input {
  &::-webkit-calendar-picker-indicator {
    @include opacity(0);
    position: $absolute;
    width: 100%;
    top: 0;
    height: 100%;
    left: 0;
  }
  &[type="date"],
  &[type="time"],
  &[type="datetime-local"] {
    background-position: 96%;
  }
  &[type="date"] {
    // background-image: url(../images/icons/calendar_icon.svg);
    background-repeat: $no-repeat;
    background-size: 18px;
  }
  &[type="time"] {
    // background-image: url(../images/icons/time_icon.svg);
    background-repeat: $no-repeat;
    background-size: 20px;
  }
  &::-webkit-inner-spin-button,
  &::-webkit-calendar-picker-indicator {
    display: $none;
    -webkit-appearance: $none;
  }
  // &:-webkit-autofill,
  // &:-webkit-autofill:hover,
  // &:-webkit-autofill:focus,
  // &:-webkit-autofill:active {
  //   -webkit-text-fill-color: $white-color;
  //   -webkit-background-clip: text !important;
  //   -webkit-box-shadow: inset 5px 5px 4px $transparent,
  //     inset -5px -5px 4px $transparent !important;
  // }
}

label {
  line-height: 1.1;
  color: $base-color;
  margin-bottom: 7px;
  @include font-size(1);
  font-weight: $font-medium;
}

// Button CSS
.btn {
  outline: 0 !important;
  cursor: $pointer;
  line-height: 1;
  position: $relative;
  white-space: nowrap;
  text-transform: $capitalize;
  @include transition(all, 0.5s, ease-in-out);
  @include border-radius(5px);
  &[disabled],
  &.disabled {
    @include opacity(0.4);
    color: $white-color;
    pointer-events: $none;
  }
  &:focus,
  &:active {
    box-shadow: $none !important;
  }
  &-lg {
    @include border-radius(10px);
    font-weight: $font-semibold;
    @include font-size(1.1);
    min-height: 58px;
    min-width: 180px;
  }
  &-md {
    font-weight: $font-semibold;
    @include font-size(1);
    padding: 1rem 2.5rem;
    overflow: $hidden;
    min-height: 50px;
    min-width: 140px;
    @include border-radius(10px);
    @media (min-width: 1441px) {
      min-height: 58px;
      @include border-radius(10px);
    }
  }
  &-sm {
    padding: 0.45rem 1rem;
    font-weight: $font-semibold;
    @include font-size(0.9);
    overflow: $hidden;
    min-width: 48px;
    min-height: 48px;
    max-height: 48px;
  }
  &-primary {
    border: 0;
    color: $white-color;
    background: radial-gradient(95.71% 95.71% at 50.16% 4.29%,
        $primary-color 0%,
        $prussian-color 100%);
    &:hover,
    &:active {
      text-decoration: $none;
      background: radial-gradient(95.71% 95.71% at 50.16% 4.29%,
          $prussian-color 0%,
          $primary-color 100%);
    }
  }
  &-secondary {
    background-color: $secondary-color;
    border-color: $secondary-color;
    color: $white-color;
    &:hover {
      border-color: $prussian-color !important;
      background-color: $prussian-color !important;
      color: $white-color;
    }
  }
  &-transparent {
    background-color: $transparent;
    border-color: $transparent;
    color: $white-color;
    &:hover {
      color: $white-color;
      border-color: $transparent !important;
      background-color: $transparent !important;
    }
  }
  &-white {
    background: $white-color;
    box-shadow: 0px 1px 16px rgba(92, 92, 92, 0.23);
  }
  &-grey {
    background: $grey-color-400;
    @include border-radius(10px);
    text-align: $center;
    padding: 8px 1rem;
    line-height: 1.3;
  }
  &-danger {
    background: $error-color;
  }
  &-badge {
    cursor: $pointer;
    max-width: 40px;
    min-width: 40px;
    min-height: 40px;
    max-height: 40px;
    text-align: $center;
    @include flexCenter;
    @include border-radius (5px);
    img {
      width: 16px;
      max-height: 16px;
    }
  }
  &-default {
    background: $base-color;
    cursor: $pointer;
  }
  &-Verified {
    background: $secondary-color;
  }
  &-Unverified {
    background: $error-color;
  }
  &-Pending {
    background: $blue-color;
  }
  &-notUploaded {
    background: $dark-grey-color;
  }
}
.badge {
  &-grey {
    line-height: 2;
    background: $grey-color-200;
    color: $base-color;
  }
}

.form {
  &-control {
    border: 1px solid $grey-color-700;
    @include border-radius(10px);
    font-weight: $font-regular;
    background: $grey-color-500;
    @include font-size(1.1);
    color: $base-color;
    min-height: 50px;
    max-height: 50px;
    padding: 0.88rem 1.2rem;
    @include transition(all, 0.5s, ease-in-out);
    @media (min-width: 1300px) {
      min-height: 60px;
    }
    &:focus {
      background-color: $white-color;
      box-shadow: 0 0 10px rgba($primary-color, 0.7);
      border-color: $primary-color;
    }
    &[readonly] {
      background: $base-color;
    }
    &.disabled,
    &:disabled {
      @include opacity(0.4);
    }
    &-sm {
      font-size: $f12 + 2;
      min-height: 46px;
      padding: 14px 15px 12px;
      border-radius: 5px !important;
    }
  }
}
textarea {
  &.form-control {
    min-height: 100px;
    @media (min-width:1300px) {
      min-height: 150px;
    }
  }
}

.white_control {
  border: 1px solid $grey-color-700;
  @include border-radius (5px);
  background: $white-color;
  padding: .53rem 1.8rem .56rem .75rem;
  min-width: 80px;
  text-align: $left;
  position: $relative;
  em {
    position: $absolute;
    right: 10px;
    top: 50%;
    @include transform (translateY(-50%));
    img {
      width: 18px;
    }
  }
  label {
    margin: 0;
  }
}

// Placeholder CSS
::placeholder {
  color: $base-color;
  opacity: 1;
}
:-ms-input-placeholder {
  color: $base-color;
}
::-ms-input-placeholder {
  color: $base-color;
}

select,
select.form-control {
  background-repeat: $no-repeat;
  background-size: 12px;
  background-position: 96% $center;
  appearance: $none;
  -webkit-appearance: $none;
  text-overflow: ellipsis;
  padding: 14px;
  padding-right: 24px;
  outline: 0 !important;
  text-transform: $capitalize;
  option {
    background: $base-color;
    color: $white-color;
  }
}
.custom-control-input[disabled]~.custom-control-label::before,
.custom-control-input:disabled~.custom-control-label::before {
  background-color: $unset;
  @include opacity(0.5);
}

.error {
  display: $block;
  color: $error-color;
  width: 100%;
  @include font-size(1);
  font-weight: $font-regular;
}

// Checkbox
.checkbox {
  position: $relative;
  display: $inline-block;
  input {
    padding: 0;
    height: 100%;
    width: 100%;
    @include opacity(0);
    position: $absolute;
    cursor: $pointer;
    z-index: 11;
    &:checked+label:after {
      content: "" !important;
      display: $block !important;
      position: $absolute;
      top: 7px;
      left: 9px;
      width: 5px;
      height: 12px;
      border: solid $primary-color;
      border-width: 0 2px 2px 0;
      @include transform(rotate(45deg));
      z-index: 1;
    }
  }
  label {
    cursor: $pointer;
    margin: 0;
    &::before {
      content: "";
      -webkit-appearance: $none;
      background-color: $transparent;
      border: 2px solid $primary-color;
      @include border-radius(5px);
      display: $inline-block;
      position: $relative;
      vertical-align: middle;
      margin-right: 9px;
      cursor: $pointer;
      padding: 9px;
    }
  }
}

.text-primary {
  color: $primary-color !important;
}


// Badge
.badge {
  display: $inline-block;
  &-md {
    min-width: 150px;
    text-align: $center;
    padding: .60rem 1.2rem;
    @include font-size (1);
    font-weight: $font-regular;
    @include border-radius (7px);
  }
  &-success {
    background-color: rgba($badge-success, .3);
    color: $badge-success;
  }
  &-danger {
    background-color: rgba($error-color, .3);
    color: $error-color;
  }
  &-info {
    background-color: rgba($base-color, .3);
    color: $base-color;
  }
}