@import '../../../assets/scss/base';

.notification_sec {
    .notification_list {
        position: $relative;
        .name {
            position: $absolute;
            left: -80px;
            top: 50%;
            padding: 10px;
            background: $white-color;
            border: 1px solid $grey-color-700;
            @include transform (translateY(-50%));
            @include font-size (1.4);
            width: 55px;
            height: 55px;
            @include border-radius (10px);
            @include flexCenter;
        }
        padding: 20px;
        @include border-radius (10px);
        margin: 0 0 20px 80px;
        background: $white-color;
        border: 1px solid $grey-color-700;
        &:nth-child(even) {
            background: $grey-color-400;
            .name {
                background: $grey-color-400;
            }
        }
        p {
            color: $base-color;
            margin-bottom: 5px;
        }
    }
}

.user_time {
    position: $relative;
    color: rgba($base-color, .5);
    font-weight: $font-semibold;
    @include font-size (.8);
    border-right: 1px solid $grey-color-700;
    padding-right: 10px;
    margin-right: 10px;
    &:last-child {
        margin-right: 0;
        padding-right: 0;
        border-right: 0;
    }
    em {
        vertical-align: text-bottom;
        svg {
            width: 12px;
        }
    }
}