@import '../../../assets/scss/base';
::root {
    --bs-modal-bg: $white-color;
    --bs-modal-bg: $grey-color-700;
}

.ReactModal {
    overflow-x: $hidden;
    &__Overlay {
        background: rgba($black-color, 0.5);
        width: 100%;
        height: 100%;
        position: $fixed;
        top: 0;
        left: 0;
        z-index: 1111;
    }
    &__Content {
        overflow-x: $hidden;
        overflow-y: $auto;
        position: $fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        outline: 0;
        z-index: 1050;
    }
}

.Modal {
    .modal-dialog {
        @media (min-width: 576px) {
            margin: 1.75rem $auto;
        }
    }
    .btn-close {
        background: var(--bs-modal-bg);
        @include opacity (1);
        cursor: $pointer;
        position: $absolute;
        right: 20px;
        top: 15px;
        z-index: 1;
        box-shadow: $none !important;
        width: 20px;
        padding: 0;
        img {
            width: 100%;
        }
    }
    .modal {
        &-md {
            max-width: 700px;
        }
        &-mds {
            max-width: 500px;
        }
        &-dialog-centered {
            min-height: calc(100% - 3.5rem);
        }
        &-content {
            background: $white-color;
            @include border-radius (1.5rem);
        }
        &-body {
            padding: 2.5rem;
        }
        &-header {
            padding: 1rem 2rem;
            border: 0;
        }
        &-footer {
            padding: 1rem 2rem;
        }
    }
}