@import '../../../assets/scss/base';

.drop_down {
    min-width: 250px;
    @include transition (all, 0.5s, ease-in-out);
    border: 1px solid $grey-color-700;
    background: $white-color;
    box-shadow: 0 0 10px rgb(0, 0, 0, .10);
    @include border-radius (10px);
    .user-body {
        max-height: 280px;
        overflow-x: $hidden;
        overflow-y: $auto;
    }
    &-list {
        border-bottom: 1px solid $grey-color-300;
        padding: 15px;
        @include flexbox;
        @include align-items ($center);
        text-transform: $capitalize;
        cursor: $pointer;
        em {
            height: 40px;
            width: 40px;
            padding: 4px;
            margin-right: 10px;
            background: $grey-color-700;
            @include border-radius (50%);
            text-align: $center;
            @include flexbox;
            @include align-items ($center);
            @include justify-content ($center);
            img {
                min-width: 20px;
                max-width: 23px;
                max-height: 20px;
            }
        }
        span {
            font-weight: $font-regular;
            text-transform: $capitalize;
            margin: $auto 0;
            @include font-size (1);
            color: $base-color;
        }
        &:hover {
            span {
                color: $secondary-color;
            }
        }
    }
    .dropdown {
        &-header {
            padding: 20px 70px;
            @include border-radius (10px 10px 0 0);
            // box-shadow: 0px 2px 10px -2px rgba($black-color, .2);
            position: $relative;
            .h6 {
                @include font-size (1.2);
                font-weight: $font-semibold;
                color: $primary-color;
            }
            .btn {
                padding: 0 10px;
                line-height: 1;
                color: $base-color;
                position: $absolute;
                font-size: 12px;
                font-weight: $font-semibold;
                top: calc(50% - 8px);
                right: 0;
                &:hover {
                    text-decoration: $underline;
                }
            }
        }
        &-body {
            padding: 0 15px;
            max-height: 270px;
            overflow-y: $auto;
            margin: 0;
            li {
                border: 1px solid $grey-color-300;
                background: $grey-color-500;
                @include border-radius (10px);
                margin-bottom: 10px;
                padding: 13px;
                color: $base-color;
                &:last-child {
                    margin-bottom: 0
                }
                .h6 {
                    font-weight: $font-semibold;
                    @include font-size (1);
                    color: $inherit;
                    margin: 0 0 4px;
                    small {
                        @include font-size (.8);
                        @include opacity (.6);
                        white-space: nowrap;
                        color: $inherit;
                        margin-left: 10px;
                    }
                }
                p {
                    color: $inherit;
                    @include opacity (.6);
                    @include font-size (.99);
                }
            }
        }
        &-footer {
            @include border-radius (0 010px 10px);
            padding: 12px 15px;
            .btn {
                font-weight: $font-semibold;
                text-decoration: $underline;
                &:hover {
                    text-decoration: $none;
                }
            }
        }
    }
}