@import ' ../../../../../assets/scss/base';

.user_profile {
    img {
        width: 100%;
        max-height: 220px;
        object-fit: $cover;
        @include border-radius (10px);
    }
}
.card {
    @include border-radius (10px);
    &-body {
        background-color: $white-color;
        @include border-radius ($inherit);
        padding: 25px;
        label {
            margin: 0 0 4px;
            &.text-small {
                display: $block;
                color: rgba($base-color, .6);
                @include font-size (.85);
            }
        }
        span {
            &.d-block {
                font-weight: $font-medium;
                em {
                    svg {
                        width: 20px;
                    }
                }
            }
        }
    }
}

.verified {
    svg {
        width: 20px;
        path {
            fill: $secondary-color;
        }
    }
}